<script>
import AlertView from '@/components/AlertView.vue'
export default {
    components: { AlertView },
    props: {
        isAddMode: {
            type: Boolean,
            default: true
        }
    },
    data: function() {
        return {
            hasSubmit: false,
            date: this.getTodayString(),
            weight: null,
            waistline: null,
            hipline: null,
            upperArm: null,
            thigh: null,
            muscle: null,
            fat: null,
            fatMass: null,
            workoutTimes: null,
            muscleStrengthChange: null,
            rm: null,
            cardiopulmonary: null,
            triathlon: null,
            showRemoveAlert: false
        }
    },
    mounted: function() {
        // let today = new Date();
        // let todayStr = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate(); 
        // this.date = todayStr;
    },
    methods: {
        closeAddFitnessData: function() {
            this.$emit('close-add-fitness-data');
        },
        storeFitnessData: function() {
            this.hasSubmit = true;
            if (!this.date || !this.weight || !this.waistline || !this.hipline || !this.upperArm || !this.thigh || !this.muscle || !this.fat
             || !this.fatMass || !this.workoutTimes) { return }
            let fitnessData = {
                date: Date.parse(this.date),
                weight: this.weight,
                waistline: this.waistline,
                hipline: this.hipline,
                upperArm: this.upperArm,
                thigh: this.thigh,
                muscle: this.muscle,
                fat: this.fat,
                fatMass: this.fatMass,
                workoutTimes: this.workoutTimes
                // muscleStrengthChange: this.muscleStrengthChange,
                // rm: this.rm,
                // cardiopulmonary: this.cardiopulmonary,
                // triathlon: this.triathlon
            }
            this.$emit('store-fitness-data', fitnessData, this.isAddMode);
            this.$emit('close-add-fitness-data');
            this.resetData();
        },
        getTodayString: function(timestamp) {
            let today = timestamp ? new Date(timestamp) : new Date();
            let year = today.getFullYear();
            let month = ("0" + (today.getMonth() + 1)).substr(-2);
            let day = ("0" + today.getDate()).substr(-2);
            let hour = ("0" + today.getHours()).substr(-2);
            let minute = ("0" + today.getMinutes()).substr(-2);
            let todayStr = `${year}-${month}-${day}T${hour}:${minute}`; 
            return todayStr
        },
        resetData: function() {
            this.hasSubmit = false;
            this.date = this.getTodayString();
            this.weight = null;
            this.waistline = null;
            this.hipline = null;
            this.upperArm = null;
            this.thigh = null;
            this.muscle = null;
            this.fat = null;
            this.fatMass = null;
            this.workoutTimes = null;
            this.muscleStrengthChange = null;
            this.rm = null;
            this.cardiopulmonary = null;
            this.triathlon = null;
        },
        setData: function(info) {
            this.date = this.getTodayString(info.date);
            this.weight = info.weight;
            this.waistline = info.waistline;
            this.hipline = info.hipline;
            this.upperArm = info.upperArm;
            this.thigh = info.thigh;
            this.muscle = info.muscle;
            this.fat = info.fat;
            this.fatMass = info.fatMass;
            this.workoutTimes = info.workoutTimes;
            this.muscleStrengthChange = info.muscleStrengthChange;
            this.rm = info.rm;
            this.cardiopulmonary = info.cardiopulmonary;
            this.triathlon = info.triathlon;
        },
        onRemoveData: function() {
            this.showRemoveAlert = true;
        },
        didRemoveData: function() {
            this.showRemoveAlert = false;
            this.$emit('did-remove-data');
        }
    }
}
</script>

