<template>
    <nav class="navbar navigation">
        <div class="container-fluid">
            <span class="navbar-brand mb-0 h1 my-text">學員健身資料系統</span>
            <button class="btn btn-outline-warning me-2 d-inline" @click="onLogout">登出</button>
        </div>
    </nav>
    <div class="container align-self-stretch p-2">
        <div class="row">
            <div class="col-auto">
                <button class="btn my-accent-color-primary btn-add-member rounded-circle" @click="onAddMember"></button>
            </div>
            <div class="col my-auto">
                <input class="form-control text-start" type="text" placeholder="請輸入學員名稱或編號" v-model="searchText">
            </div>
            <div class="col-auto my-auto">
                <button class="btn my-accent-color-primary" @click="onRemoveSearchText">清除</button>
            </div>
            <div class="col-auto my-auto">
                <button class="btn my-accent-color-primary" @click="onExport">報表輸出</button>
            </div>
            <div class="col-auto my-auto dropdown">
                <button class="btn my-accent-color-secondary btn-sort" id="dropdownMenuButton1" data-bs-toggle="dropdown" aria-expanded="false">{{ sortName }}
                    <i class="btn-sort-img" :class="sortDown ? '' : 'icon-sort-reverse'"></i>
                </button>
                <ul class="dropdown-menu" aria-labelledby="dropdownMenuButton1">
                    <li><button class="dropdown-item text-center" href="#" @click="onSortButton('name')">名稱</button></li>
                    <li><button class="dropdown-item text-center" href="#" @click="onSortButton('age')">年齡</button></li>
                </ul>
            </div>
        </div>
        <div class="row mt-2">
            <div v-if="filteredInfosKeys.length > 0">
                <member-cell class="col-12 mb-3" v-for="key in filteredInfosKeys" :key="key" v-bind:info="memberInfos[key]" @click="onClickCell(key)" @delete-member="deleteMember(key)"/>
            </div>
            <div v-else>
                <h1>無資料</h1>
            </div>
        </div>
    </div>
    <add-member class="" title="新增學員" v-show="showAddMember" @store-member="storeMember" @close-view="showAddMember = false"></add-member>
    <alert-view title="確認" :message="'確認刪除 ' + infoToDelete.name + ' 會員？'" :subMessage="'會員健身資料會永久刪除'" v-show="showAlertView" @cancel-handler="showAlertView = false" @confirm-handler="onConfirmDeleteInfo"></alert-view>
    <alert-view title="確認" :message="'確認登出？'" v-show="showLoginoutView" @cancel-handler="showLoginoutView = false" @confirm-handler="onConfirmLogout"></alert-view>
</template>

<script>
import MemberCell from '@/components/MemberCell.vue'
import AddMember from '@/components/AddMember.vue'
import AlertView from '@/components/AlertView.vue'
import { getMemberList, addMemberData, removeMemberData } from '@/js/FirebaseDatabase.js'
import storage from '@/js/Storage.js';
// import FakeMemberDataJson from '@/json/fake_member_data.json'
const XLSX = require("xlsx");

export default {
  components: { MemberCell, AddMember, AlertView },
  data: function() {
      return {
          showAddMember: false,
          showAlertView: false,
          showLoginoutView: false,
          searchText: '',
          memberInfos: {},
          keyToDelete: null,
          trainerId: '',
          sortItem: 'name',
          sortDown: true
      }
  },
  computed: {
      filteredInfosKeys: function() {
          let sortInfos = Object.fromEntries(
              Object.entries(this.memberInfos)
              // filtering items by keyword
              .filter(([, value]) => {
                  if (this.searchText) {
                      return (value.name.includes(this.searchText) || value.id == this.searchText);
                  } else {
                      return true;
                  }
              })
              // sorting items by parameter
              .sort(([, a], [, b]) => {
                  let valueA, valueB;

                  if (this.sortItem == "name") {
                      valueA = a.name;
                      valueB = b.name;
                  } else if (this.sortItem == "age") {
                      valueA = a.birthday;
                      valueB = b.birthday;
                  }

                  if (this.sortDown) {
                      return valueA > valueB ? 1 : -1;
                  } else {
                      return valueA < valueB ? 1 : -1;
                  }
              })
          );
        //   console.log('sortInfos: ' + JSON.stringify(sortInfos));
          // return result keys
          return Object.keys(sortInfos);
      },
      infoToDelete: function() {
          let info = this.memberInfos[this.keyToDelete];
          return info ?? {}
      },
      sortName: function() {
          if (this.sortItem == 'name') {
              return '名稱';
          } else if (this.sortItem == 'age') {
              return '年齡';
          } else {
              return '名稱'
          }
      }
  },
  mounted: function() {
      storage.checkLoginState();
      let trainerId = storage.getTrainerId();
      this.trainerId = trainerId;
    //   console.log('trainerId: ' + this.trainerId);
      getMemberList(this.trainerId, (res) => {
          if (res) {
              this.memberInfos = res;
          } else {
              this.memberInfos = {};
          }
      });
  },
  methods: {
      onClickCell: function(key) {
        //   console.log(`Selected member key: ${key}`);
          this.$router.push({ name: 'MemberDetail', params: { memberId: key} });
      },
      onAddMember: function() {
          this.showAddMember = true;
      },
      storeMember: function(info) {
          this.showAddMember = false;
          addMemberData(info, this.trainerId);
      },
      deleteMember: function(key) {
        this.keyToDelete = key;
          this.showAlertView = true;
      },
      onRemoveSearchText: function() {
          this.searchText = '';
      },
      closeAlertView: function() {
          this.showAlertView = false;
      },
      onConfirmDeleteInfo() {
        removeMemberData(this.keyToDelete);
        this.closeAlertView();
        this.keyToDelete = null;
      },
      onLogout: function() {
          this.showLoginoutView = true;
      },
      onConfirmLogout: function() {
          storage.removeTrainerId();
          this.$router.push({ name: 'Login' });
      },
      onSortButton: function(value) {
          if (value == this.sortItem) {
              this.sortDown = !this.sortDown;
          } else {
              this.sortItem = value;
          }
      },
      onExport: function() {
            if (this.memberInfos.length < 1) { return }
            let workbook = XLSX.utils.book_new();
            let data = [["#", "姓名", "生日", "年齡", "性別", "職業", "身高(CM)", "體重(KG)", "體脂率(%)", "BMI", "學員預期目標", "教練調整目標"]];
            let number = 1;
            for (let index in this.memberInfos) {
                let rowData = [];
                let info = this.memberInfos[index];
                rowData.push(number);
                rowData.push(info.name);
                rowData.push(new Date(info.birthday).toLocaleDateString());
                rowData.push(this.convertToAgeFromBirthday(info.birthday));
                rowData.push(info.gender);
                rowData.push(info.career);
                rowData.push(info.height);
                rowData.push(info.weight);
                rowData.push(info.fat);
                rowData.push(info.bmi);
                rowData.push(info.sportGoal);
                rowData.push(info.suggestedGoal);
                data.push(rowData);
                number++;
            }
            var worksheet = XLSX.utils.aoa_to_sheet(data);
            XLSX.utils.book_append_sheet(workbook, worksheet, '會員清單');
            XLSX.writeFile(workbook, `${new Date().toLocaleDateString()}_會員清單.xlsx`);
      },
      convertToAgeFromBirthday: function(birthday) {
            var ageDifMs = Date.now() - new Date(birthday);
            var ageDate = new Date(ageDifMs); // miliseconds from epoch
            return Math.abs(ageDate.getUTCFullYear() - 1970);
        }
  }
}
</script>

<style>
.navigation {
    height: 100px;
    background: linear-gradient(to right, rgba(0, 0, 0, 1), rgba(0, 0, 0, 0) ), url('~@/assets/bg_home.jpeg') no-repeat fixed;
    background-position: center -330px;
    background-size: cover;
}
.btn-add-member {
    height: 60px;
    width: 60px;
    background-image: url('~@/assets/icon_add_member.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
}
.btn-sort {
    widows: 120px;
}
.btn-sort-img {
    display: inline-block;
    height: 15px;
    width: 20px;
    background-image: url('~@/assets/icon_sort_down.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}
.icon-sort-reverse {
    transform: scaleY(-1);
}
</style>