<template>
  <div class="dimming-view">
      <div class="container">
        <div class="align-items-center row vh-100 d-inline-flex overflow-scroll">
            <div class="col my-background-color-primary ps-4 pe-4 rounded-3">
                <div class="row mb-1">
                    <div class="col-auto">
                        <button class="btn my-btn-close" @click="closeAddMember"></button>
                    </div>
                    <div class="col my-auto">
                        <p class="h5 my-auto">{{ title }}</p>
                    </div>
                    <div class="col-auto my-auto">
                        <button class="btn my-accent-color-primary" @click="storeMember">{{ isAddMode ? '新增' : '儲存' }}</button>
                    </div>
                </div>
                <!-- input data -->
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="nameInput" class="form-label m-0 h5">姓名</label>
                    </div>
                    <div class="col-8">
                        <input id="nameInput" class="form-control text-center" type="text" v-model="name" v-bind:class="{ 'border': hasSubmit && !name, 'border-danger': hasSubmit && !name }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="birthdayInput" class="form-label m-0 h5">生日</label>
                    </div>
                    <div class="col-8">
                        <input id="birthdayInput" class="form-control text-center" type="date" v-model="birthday" v-bind:class="{ 'border': hasSubmit && !birthday, 'border-danger': hasSubmit && !birthday }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="genderInput" class="form-label m-0 h5">性別</label>
                    </div>
                    <div class="col-8">
                        <select class="form-select text-center" v-model="gender">
                            <option value="男">男</option>
                            <option value="女">女</option>
                        </select>
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="heightInput" class="form-label m-0 h5">身高(cm)</label>
                    </div>
                    <div class="col-8">
                        <input id="heightInput" class="form-control text-center" type="number" v-model="height" v-bind:class="{ 'border': hasSubmit && !height, 'border-danger': hasSubmit && !height }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="weightInput" class="form-label m-0 h5">體重(kg)</label>
                    </div>
                    <div class="col-8">
                        <input id="weightInput" class="form-control text-center" type="number" v-model="weight" v-bind:class="{ 'border': hasSubmit && !weight, 'border-danger': hasSubmit && !weight }">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="careerInput" class="form-label m-0 h5">職業</label>
                    </div>
                    <div class="col-8">
                        <input id="careerInput" class="form-control text-center" type="text" v-model="career">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="sportFrequencyInput" class="form-label m-0 h5">運動頻率</label>
                    </div>
                    <div class="col-8">
                        <input id="sportFrequencyInput" class="form-control text-center" type="number" v-model="sportFrequency">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="sportGoalInput" class="form-label m-0 h5">學員預期目標</label>
                    </div>
                    <div class="col-8">
                        <input id="sportGoalInput" class="form-control text-center" type="text" v-model="sportGoal">
                    </div>
                </div>
                <div class="row mb-2">
                    <div class="col my-auto text-start">
                        <label for="suggestedGoalInput" class="form-label m-0 h5">教練調整目標</label>
                    </div>
                    <div class="col-8">
                        <input id="suggestedGoalInput" class="form-control text-center" type="text" v-model="suggestedGoal">
                    </div>
                </div>
            </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
    props: {
        title: String,
        isAddMode: {
            type: Boolean,
            default: true
        },
        info: {
            type: Object,
            default: Object
        }
    },
    watch: {
        info: function(newValue) {
            this.setupData(newValue);
        }
    },
    data: function() {
        return {
            hasSubmit: false,
            name: '',
            birthday: '',
            gender: '男',
            height: null,
            weight: null,
            career: '',
            sportFrequency: null,
            sportGoal: '',
            suggestedGoal: '',
            bmi: null,
            fat: null
        }
    },
    mounted: function() {
        if (!this.isAddMode || this.info) {
            this.setupData(this.info);
        }
    },
    methods: {
        setupData: function(info) {
            this.name = info.name;
            this.birthday = info.birthday;
            this.gender = info.gender;
            this.height = Number(info.height);
            this.weight = Number(info.weight);
            this.career = info.career;
            this.sportFrequency = Number(info.sportFrequency);
            this.sportGoal = info.sportGoal;
            this.suggestedGoal = info.suggestedGoal;
            this.bmi = info.bmi;
            this.fat = info.fat;
        },
        closeAddMember: function() {
            this.$emit('close-view');
            this.resetInfo();
        },
        storeMember: function() {
            this.hasSubmit = true;
            if (!this.name || !this.birthday || !this.height || !this.weight) { return }
            let info = {
                id: new Date().getTime(),
                name: this.name,
                birthday: this.birthday,
                gender: this.gender,
                height: Number(this.height),
                weight: Number(this.weight),
                career: this.career,
                sportFrequency: Number(this.sportFrequency),
                sportGoal: this.sportGoal,
                suggestedGoal: this.suggestedGoal,
                bmi: this.bmi,
                fat: this.fat
            };
            this.$emit('store-member', info);
            this.resetInfo();
        },
        resetInfo: function() {
            if (this.isAddMode) {
                this.hasSubmit = false;
                this.name = '';
                this.birthday = '';
                this.gender = '男';
                this.height = null;
                this.weight = null;
                this.career = '';
                this.sportFrequency = null;
                this.sportGoal = '';
                this.suggestedGoal = '';
                this.bmi = null;
                this.fat = null;
            }
        }
    }
}
</script>

<style>
.my-btn-close {
    height: 60px;
    width: 60px;
    background-image: url('~@/assets/icon_close.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
}
</style>