<template>
  <div class="container h-100">
        <div class="row pt-2 pb-2">
            <div class="col-auto my-auto">
                <button class="btn btn-back my-accent-color-primary rounded-circle" @click="onBack"></button>
            </div>
            <div class="col my-background-color-secondary rounded-3 my-view-shadow">
                <div class="row h-100">
                    <div class="col">
                        <div class="row row-cols-1 row-cols-sm-2 row-cols-md-2 row-cols-lg-5 h-100">
                            <div class="col-12 col-md my-text h3 my-auto">
                                {{ memberInfo.name ?? "---" }}
                            </div>
                            <div class="col-12 col-md my-text h3 my-auto">
                                {{ age  }}歲
                            </div>
                            <div class="col-12 col-md my-text h3 my-auto">
                                {{ new Date(memberInfo.birthday).toLocaleDateString() }}
                            </div>
                            <div class="col-12 col-md my-text h3 my-auto">
                                {{ memberInfo.gender }}
                            </div>
                            <div class="col-12 col-md my-text h3 my-auto">
                                {{ memberInfo.height }}cm
                            </div>
                        </div>
                    </div>
                    <div class="col-auto my-auto">
                        <button class="btn btn-spreadsheet rounded-circle my-primary-style" @click="onExport"></button>
                    </div>
                    <div class="col-auto my-auto">
                        <button class="btn btn-edit rounded-circle my-primary-style" @click="onEdit"></button>
                    </div>
                </div>
            </div>
            <div class="col-auto my-auto">
                <button class="btn btn-add-data my-accent-color-secondary rounded-circle" @click="onAddData"></button>
            </div>
        </div>
        <div v-if="dataKeys.length > 0" class="row gy-2 gx-5">
            <div class="col-12 col-md-7 col-xl-6 offset-xl-1">
                <div class="row my-background-color-secondary rounded-3 h-100 my-view-shadow p-2">
                    <data-view 
                    :dataIndex="dataIndex"
                    :fitnessData="fitnessDataArr" 
                    :currentData="currentData"
                    :previousData="previousData"
                    :height="memberInfo.height" 
                    :hasPreviousData="hasPreviousData"
                    :hasNextData="hasNextData"
                    ref="dataView"
                    @increment-data-index="onIncrementDataIndex"
                    @decrement-data-index="onDecrementDataIndex"
                    @set-data-index="setDataIndex"
                    @on-edit="onEditFitnessData"
                    />
                </div>
            </div>
            <div class="col-12 col-md-5 col-xl-4">
                <row class="row align-items-center h-100 my-background-color-secondary rounded-3 my-view-shadow">
                    <div class="">
                        <img id="img-avatar" class="img-fluid img-transparent" :src="avatarImage" @load="onImageLoad"/>
                        <!-- <p class="my-text h1">暫無圖片</p> -->
                    </div>
                </row>
            </div>
        </div>
        <div v-else class="row gy-2">
            <p class="h1">無資料</p>
        </div>
  </div>
  <add-member class="" title="編輯學員" :info="memberInfo" :isAddMode="isAddMode" v-show="showAddMember" @store-member="storeMember" @close-view="showAddMember = false"></add-member>
  <add-fitness-data-view 
  ref="fitness" 
  v-show="showAddFitnessData" 
  :isAddMode="isAddModeForData" 
  @close-add-fitness-data="showAddFitnessData = false" 
  @store-fitness-data="storeFitnessData"
  @did-remove-data="didRemoveData"
  ></add-fitness-data-view>
</template>

<script>
import DataView from '@/components/DataView.vue'
import AddMember from '@/components/AddMember.vue'
import AddFitnessDataView from '@/components/AddFitnessDataView.vue'
import { getMemberBy, updateMemberData, addMemberFitnessData, getMemberFitnessData, updateMemberFitnessData, removeMemberFitnessData } from '@/js/FirebaseDatabase.js'
// fake data
// import FakeMemberData from '@/json/fake_member_data.json'
// import FakeFitnessData from '@/json/fake_fitness_data.json'
import storage from '@/js/Storage.js';
import animation from '@/js/Animation.js';
const manStandardImage = require('@/assets/img_man_standard.png');
const manOverweightImage = require('@/assets/img_man_overweight.png');
const manUnderweightImage = require('@/assets/img_man_underweight.png');
const womenStandardImage = require('@/assets/img_women_standard.png');
const womenOverweightImage = require('@/assets/img_women_overweight.png');
const womenUnderweightImage = require('@/assets/img_women_underweight.png');
const XLSX = require("xlsx");

export default {
  components: { DataView, AddMember, AddFitnessDataView },
    data: function() {
        return {
            memberId: '',
            showAddMember: false,
            showAddFitnessData: false,
            isAddMode: false,
            isAddModeForData: true,
            dataIndex: 0,
            memberInfo: {},
            // fitnessData: FakeFitnessData
            fitnessData: {}
        }
    },
    mounted: function() {
        // this.memberInfo = this.info;
        let memberId = this.$route.params.memberId;
        this.memberId = memberId;
        // console.log('memberId: ' + memberId);
        getMemberBy(memberId, (info) => {
            if (info) {
                // console.log('info: ' + JSON.stringify(info[memberId]));
                this.memberInfo = info[memberId];
            } else {
                this.$router.push({ name: 'List' });
            }
        });
        getMemberFitnessData(memberId, (data) => {
            // console.log('data: ' + JSON.stringify(data));
            // let arr = [];
            // Object.keys(data).forEach((key) => {
            //     let obj = {};
            //     obj[key] = data[key];
            //     arr.push(obj);
            // });
            // console.log('data: ' + JSON.stringify(arr));
            if (data) {
                this.fitnessData = data;
                // console.log('keys: ' + JSON.stringify(this.dataKeys));
                this.dataIndex = this.lastIndex;
            }
        })
    },
    computed: {
        age: function() {
            var ageDifMs = Date.now() - new Date(this.memberInfo.birthday);
            var ageDate = new Date(ageDifMs); // miliseconds from epoch
            return Math.abs(ageDate.getUTCFullYear() - 1970);
        },
        dataKeys: function() {
            let sortInfos = Object.fromEntries(
              Object.entries(this.fitnessData)
              // sorting items by parameter
              .sort(([, a], [, b]) => {
                  return a.date - b.date;
              })
            );
            let keys = Object.keys(sortInfos);
            return keys;
        },
        // fitnessDataArr: function() {
        //     let arr = [];
        //     Object.keys(this.fitnessData).forEach((key) => {
        //         arr.push(this.fitnessData[key]);
        //     });
        //     arr.sort((a, b) => {
        //         // earier date will be put in the first
        //         return a.date - b.date
        //     })
        //     return arr;
        // },
        currentData: function() {
            if (this.fitnessData && this.dataKeys.length > 0) {
                return this.fitnessData[this.dataKeys[this.dataIndex]];
            } else {
                return {}
            }
        },
        previousData: function() {
            if (this.fitnessData && this.dataKeys.length > 1) {
                return this.fitnessData[this.dataKeys[this.dataIndex - 1]];
            } else {
                return {}
            }
        },
        avatarImage: function() {
            if (this.memberInfo) {
                // console.log('BMI: ' + this.lastestBmi);
                switch (true) {
                    case (this.lastestBmi < 18.5):
                        // underweight
                        return this.memberInfo.gender == '男' ? manUnderweightImage : womenUnderweightImage
                    case (this.lastestBmi > 24.0):
                        // overweight
                        return this.memberInfo.gender == '男' ? manOverweightImage : womenOverweightImage
                    default:
                        // standard
                        return this.memberInfo.gender == '男' ? manStandardImage : womenStandardImage
                }

                // if (this.memberInfo.gender == "男") {
                //     // man avatar
                    
                // } else {
                //     // womem avatar
                    
                // }
                // return this.memberInfo.gender == "男" ? manStandardImage : womenStandardImage;
            } else {
                return '';
            }
        },
        lastIndex: function() {
            return (this.dataKeys.length ?? 1) - 1;
        },
        lastestBmi: function() {
            if (this.dataKeys.length > 0) {
                let lastData = this.lastestData;
                let height = this.memberInfo.height;
                let weight = lastData.weight;
                if (height && weight) { 
                    let original = weight / Math.pow(height / 100, 2);
                    let rounded = Math.round(original * 10) / 10;
                    return rounded.toFixed(1)
                 } else {
                    return 0.0
                 }
            } else {
                return 0.0
            }
        },
        lastestFat: function() {
            if (this.lastestData) {
                return this.lastestData.fat;
            } else {
                return 0.0
            }
        },
        lastestData: function() {
            if (this.dataKeys.length > 0) {
                let lastData = this.fitnessData[this.dataKeys.at(-1)];
                return lastData;
            } else {
                return {};
            }
        },
        hasPreviousData: function() {
            return this.dataIndex > 0;
        },
        hasNextData: function() {
            return this.dataKeys.length > (this.dataIndex + 1);
        }
    },
    methods: {
        onBack: function() {
            this.$router.push('/list');
        },
        onAddData: function() {
            this.showAddFitnessData = true;
            this.isAddModeForData = true;
            this.$refs.fitness.resetData();
        },
        onEdit: function() {
            this.showAddMember = true;
        },
        storeMember: function(info) {
            // console.log('key: ' + this.memberId);
            // this.memberInfo.name = info.name;
            // this.memberInfo.birthday = info.birthday;
            // this.memberInfo.gender = info.gender;
            // this.memberInfo.height = info.height;
            // this.memberInfo.weight = info.weight;
            // this.memberInfo.career = info.career;
            // this.memberInfo.sportFrequancy = info.sportFrequancy;
            // this.memberInfo.sportGoal = info.sportGoal;
            info['trainerId'] = storage.getTrainerId();
            info['fat'] = this.lastestFat;
            info['bmi'] = this.bmi(this.lastestData.weight, info.height);
            updateMemberData(this.memberId, info);
            
            this.showAddMember = false;
        },
        storeFitnessData(data, isAddMode) {
            data['height'] = this.memberInfo.height;
            if (isAddMode) {
                addMemberFitnessData(data, this.memberId);
            } else {
                data['memberId'] = this.memberId;
                updateMemberFitnessData(data, this.dataKeys[this.dataIndex]);
            }
            // console.log(data);
            // this.fitnessData.push(data);
            // this.fitnessData.sort((a, b) => {
            //     return a.date > b.date ? 1 : -1
            // })
            // let newIndex = this.fitnessData.findIndex(d => d.date == data.date);
            // this.$refs.dataView.showLastData();
            this.showAddFitnessData = false;
            this.memberInfo['fat'] = this.lastestFat;
            this.memberInfo['bmi'] = this.lastestBmi;

            updateMemberData(this.memberId, this.memberInfo);
        },
        onEditFitnessData() {
            this.showAddFitnessData = true;
            this.isAddModeForData = false;
            this.$refs.fitness.setData(this.currentData);
        },
        onImageLoad: function() {
            animation.moveIn('#img-avatar', { direction: 'right' });
        },
        bmi: function(weight, height) {
            if (!height && !weight) { return '---' }
            let original = weight / Math.pow(height / 100, 2);
            let rounded = Math.round(original * 10) / 10;
            return rounded.toFixed(1)
        },
        onExport: function() {
            if (this.dataKeys.length < 0) { return }
            let workbook = XLSX.utils.book_new();
            let member = this.memberInfo;
            let data = [
                ["姓名", "生日", "性別", "職業", "身高(CM)", "學員預期目標", "教練調整目標"],
                [member.name, new Date(member.birthday).toLocaleDateString(), member.gender, member.career, member.height, member.sportGoal, member.suggestedGoal],
                ["#", "測驗時間", "腰圍度", "臀圍度", "上臂圍度", "大腿圍度", "體重(CM)", "體脂率(%)", "BMI", "體脂肪質量", "骨骼肌質量", "新增本周運動次數"]
                ];
            let number = 1;
            let reverseKeys = this.dataKeys.slice().reverse();
            for (let index in reverseKeys) {
                let rowData = [];
                let info = this.fitnessData[reverseKeys[index]];
                rowData.push(number);
                rowData.push(new Date(info.date).toLocaleDateString());
                rowData.push(info.waistline);
                rowData.push(info.hipline);
                rowData.push(info.upperArm);
                rowData.push(info.thigh);
                rowData.push(info.weight);
                rowData.push(info.fat);
                rowData.push(this.bmi(info.weight, member.height));
                rowData.push(info.fatMass);
                rowData.push(info.muscle);
                rowData.push(info.workoutTimes);
                data.push(rowData);
                number++;
            }
            var worksheet = XLSX.utils.aoa_to_sheet(data);
            XLSX.utils.book_append_sheet(workbook, worksheet, '健身數據資料');
            XLSX.writeFile(workbook, `${new Date().toLocaleDateString()}_${member.name}_健身數據.xlsx`);
        },
        onIncrementDataIndex: function() {
            this.dataIndex++;
        },
        onDecrementDataIndex: function() {
            this.dataIndex--;
        },
        setDataIndex: function(index) {
            this.dataIndex = index;
        },
        didRemoveData: function() {
            this.showAddFitnessData = false;
            removeMemberFitnessData(this.dataKeys[this.dataIndex], () => {
                getMemberFitnessData(this.memberId, (data) => {
                    if (data) {
                        this.fitnessData = data;
                        this.dataIndex = this.lastIndex;
                    } else {
                        this.fitnessData = {};
                    }
                })
            });
        }
    }
}
</script>

<style>
.lower-row {
    position: relative;
    bottom: 0;
}
.btn-back {
    height: 60px;
    width: 60px;
    background-image: url('~@/assets/icon_back.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
}
.btn-add-data {
    height: 60px;
    width: 60px;
    background-image: url('~@/assets/icon_add_data.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
}
.btn-edit {
    height: 50px;
    width: 50px;
    background-image: url('~@/assets/icon_edit.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
}
.btn-spreadsheet {
    height: 50px;
    width: 50px;
    background-image: url('~@/assets/icon_spreadsheet.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
}
.img-avatar {
    background-image: url('~@/assets/img_man_standard.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
}
.img-transparent {
    opacity: 0;
}
</style>