<template>
  <div class="d-flex">
      <i class="icon-item align-self-center" :style="{ backgroundImage: 'url(' + iconImage + ')' }"></i>
      <p class="my-second-text fs-3 flex-fill text-start my-auto ps-2">{{ title }}</p>
      <p class="my-text fs-3 my-auto fw-bold" ref="valueDiv">{{ value }}</p>
      <p class="my-text fs-3 my-auto fw-bold ps-2"> {{ unit }} </p>
      <i ref="iconTrendency" :class="trendClass"></i>
  </div>
</template>

<script>
const animation = require('@/js/Animation.js');

export default {
    props: {
        title: String,
        value: Number,
        unit: String,
        icon: String,
        isIncreasing: Boolean
    },
    watch: {
        value: function(newValue, oldValue) {
            animation.countDigitals(this.$refs.valueDiv, oldValue, newValue);
        }
    },
    mounted: function() {
        animation.countDigitals(this.$refs.valueDiv, 0, this.value);
    },
    computed: {
        iconImage: function() {
            let img = require('@/assets/' + this.icon);
            return img
        },
        trendClass: function() {
            if (this.isIncreasing == true) {
                return "icon-trend-red-up"
            } else if (this.isIncreasing == false) {
                return "icon-trend-green-down"
            } else {
                return "icon-trend-empty"
            }
        }
    },
    methods: {
        fireAnimation: function() {
            if (this.isIncreasing == true) {
                animation.moveIn(this.$refs.iconTrendency, { direction: 'top', easing: 'easeOutElastic', scaleY: 1});
            } else if (this.isIncreasing == false) {
                animation.moveIn(this.$refs.iconTrendency, { direction: 'top', easing: 'easeOutElastic', scaleY: -1});
            }   
        }
    },
    updated() {
        this.fireAnimation();
    }
}
</script>

<style>
.icon-item {
    height: 50px;
    width: 40px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: 35px;
}
.icon-trend-red-up {
    height: 50px;
    width: 40px;
    background-image: url('~@/assets/icon_red_rising_arrow.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px;
    transform: scaleY(1);
}
.icon-trend-red-down {
    height: 50px;
    width: 40px;
    background-image: url('~@/assets/icon_red_rising_arrow.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px;
    transform: scaleY(-1);
}
.icon-trend-green-up {
    height: 50px;
    width: 40px;
    background-image: url('~@/assets/icon_green_rising_arrow.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px;
}
.icon-trend-green-down {
    height: 50px;
    width: 40px;
    background-image: url('~@/assets/icon_green_rising_arrow.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 20px;
    transform: scaleY(-1);
}
.icon-trend-empty {
    height: 50px;
    width: 40px;
}
</style>