// import CryptoJs from 'crypto-js'

// constant values

const KEY_TRAINER_ID = "trainerId";

// login validation

module.exports.checkLoginState = _checkLoginState;
function _checkLoginState() {
    let trainerId = _getTrainerId();
    if (!trainerId) {
        this.$router.push({ name: 'Login' });
    }
}

// methods

module.exports.getTrainerId = _getTrainerId;
function _getTrainerId() {
    let value = _getStorage(KEY_TRAINER_ID);
    return value
}

module.exports.setTrainerId = _setTrainerId;
function _setTrainerId(trainerId) {
    _setStorage(KEY_TRAINER_ID, trainerId);
}

module.exports.removeTrainerId = _removeTrainerId;
function _removeTrainerId() {
    _removeStorage(KEY_TRAINER_ID);
}

// localStorage base methods

function _setStorage(key, value) {
    localStorage.setItem(key, value);
}

function _getStorage(key) {
    let value = localStorage.getItem(key);
    return value
}

function _removeStorage(key) {
    localStorage.removeItem(key);
}
