// Login

function login(account, password, callback) {
    let tk = account + '-' + password;
    // console.log('Login!!');
    let q = query(trainerRef, orderByChild('token'), equalTo(tk));
    onValue(q, (res) => {
        let data = res.val();
        // let debug = JSON.stringify(data);
        // console.log('trainer: ' + debug);
        let info;
        if (data) {
            // remove null in an array
            if (Array.isArray(data)) {
                data = data.filter(n => n);
                info = data[0];
            } else {
                let keys = Object.keys(data);
                let key;
                if (keys.length > 0) {
                    key = keys[0];
                }
                info = data[key];
            }
            // console.log('data: ' + data);
            callback(info);
        } else {
            callback(null);
        }
    });
}

// Member

function getMemberList(trainerId, callback) {
    let q = query(memberRef, orderByChild('trainerId'), equalTo(Number(trainerId)));
    // let q = query(memberRef);
    onValue(q, (res) => {
        let data = res.val();
        // let debug = JSON.stringify(data);
        // let arr = _objectToArray(data) ?? [];
        // _printObject(data);
        if (data) {
            callback(data);
        } else {
            // console.log('No data');
            callback(null);
        }
    })
}

function getMemberBy(id, callback) {
    let q = query(memberRef, orderByKey(), equalTo(id));
    onValue(q, (res) => {
        let data = res.val();
        // let debug = JSON.stringify(data);
        // console.log('debug: ' + debug);
        if (data) {
            callback(data);
        } else {
            // console.log('no member');
            callback(null);
        }
    })
}

function addMemberData(info, trainerId) {
    let newRef = push(memberRef);
    // let id = newRef.key;
    // console.log('new id: ' + id);
    set(newRef, {
        name: info.name,
        birthday: info.birthday,
        gender: info.gender,
        height: Number(info.height),
        weight: Number(info.weight),
        career: info.career,
        sportFrequency: Number(info.sportFrequency),
        sportGoal: info.sportGoal,
        suggestedGoal: info.suggestedGoal,
        trainerId: Number(trainerId)
    });
}

function updateMemberData(key, info) {
    let oldRef = ref(db, '/members/' + key);
    set(oldRef, {
        name: info.name,
        birthday: info.birthday ?? 0,
        gender: info.gender ?? '',
        height: Number(info.height ?? 0),
        weight: Number(info.weight ?? 0),
        career: info.career ?? '',
        sportFrequency: Number(info.sportFrequency ?? 0),
        sportGoal: info.sportGoal ?? '',
        suggestedGoal: info.suggestedGoal ?? '',
        trainerId: Number(info.trainerId),
        fat: Number(info.fat ?? 0),
        bmi: Number(info.bmi ?? 0)
    })
}

function removeMemberData(memberId) {
    let removeRef = ref(db, '/members/' + memberId);
    remove(removeRef);
}

// Member fitness

function getMemberFitnessData(memberId, callback) {
    let q = query(dataRef, orderByChild("memberId"), equalTo(memberId));
    onValue(q, (res) => {
        let data = res.val();
        // let debug = JSON.stringify(data);
        // let arr = _objectToArray(data) ?? [];
        // _printObject(data);
        if (data) {
            callback(data);
        } else {
            // console.log('No data');
            callback(null);
        }
    })
}

function addMemberFitnessData(info, memberId) {
    let newRef = push(dataRef);
    set(newRef, {
        date: info.date,
        height: info.height,
        weight: info.weight,
        waistline: info.waistline,
        hipline: info.hipline,
        upperArm: info.upperArm,
        thigh: info.thigh,
        muscle: info.muscle,
        fat: info.fat,
        fatMass: info.fatMass,
        workoutTimes: info.workoutTimes,
        // muscleStrengthChange: info.muscleStrengthChange,
        // rm: info.rm,
        // cardiopulmonary: info.cardiopulmonary,
        // triathlon: info.triathlon,
        memberId: memberId
    });
}

function updateMemberFitnessData(info, key, callback) {
    let updates = {};
    updates['/' + key] = info
    update(dataRef, updates)
    .then(() => {
        callback();
    });
}

function removeMemberFitnessData(fitnessDataId, callback) {
    let removeRef = ref(db, '/data/' + fitnessDataId)
    remove(removeRef)
    .then(() => {
        callback();
    });
}

