const anime = require('animejs').default;


module.exports.countDigitals = function(element, from, to, round) {
    anime({
        targets: element,
        easing: 'linear',
        duration: 300,
        autoplay: true,
        innerHTML: [from , to],
        round: round ? round : 1,
        begin: function() {
            // ...
        },
        complete: function() {
            // ...
        }
    });
}

// config { direction: 'left' }
module.exports.moveIn = function(element, config) {
    let tX, tY = 0;
    let easing = config.easing ?? 'easeOutQuint';
    switch (config.direction.toLowerCase()) {
        case 'left': tX = -100; break;
        case 'right': tX = 100; break;
        case 'top': tY = 100; break;
        case 'bottom': tY = -100; break;
    }
    let animeConfig = {
        targets: element,
        easing: easing,
        translateX: [tX, 0],
        translateY: [tY, 0],
        opacity: [0, 1],
        duration: 800,
        scaleY: config.scaleY ?? 1,
        autoplay: true,
        begin: function() {
            // ...
        },
        complete: function() {
            // ...
        }
    }
    anime(animeConfig);
}