<template>
  <div class="login-container">
    <div class="container">
        <div class="row align-items-center justify-content-center" style="height: 100vh;">
            <div class="col-10 col-md-7 col-lg-5">
                <h1 class="my-text">學員健身資料系統</h1>
                <div class="rounded-3 my-background-color-secondary my-view-shadow">
                    <form class="ps-5 pe-5 pt-4 pb-4">
                        <div class="mb-2">
                            <label for="exampleInputEmail1" class="d-flex form-label my-text">教練帳號</label>
                            <input type="text" class="form-control" id="exampleInputEmail1" placeholder="請輸入帳號" v-model="account" v-bind:class="{ border: hasSubmit && !account, 'border-danger': hasSubmit && !account }">
                        </div>
                        <div class="mb-4">
                            <label for="exampleInputPassword1" class="d-flex form-label my-text">密碼</label>
                            <input type="password" class="form-control" id="exampleInputPassword1" placeholder="請輸入密碼" v-model="password" v-bind:class="{ border: hasSubmit && !password, 'border-danger': hasSubmit && !password }">
                        </div>
                        <div class="d-grid">
                            <p class="my-warning-text" v-show="error">{{ error }}</p>
                            <label class="form-label"></label>
                            <button type="submit" class="btn my-primary-style" @click="onLogin">登入</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import { login } from '@/js/FirebaseDatabase.js'
import storage from '@/js/Storage.js';

export default {
    data: function() {
        return {
            hasSubmit: false,
            account: "",
            password: "",
            error: ""
        }
    },
    mounted: function() {
        // console.log('Data: ' + getData());
        // getData();
    },
    methods: {
        onLogin: function(e) {
            this.error = '';
            this.hasSubmit = true;
            if (!this.account || !this.password) { 
                this.error = "請輸入正確的帳號和密碼";
                return 
            }
            e.preventDefault();
            // console.log(`Account: ${this.account}, Password: ${this.password}`);
            login(this.account, this.password, (res) => {
                // console.log('res: ' + JSON.stringify(res) ?? "");
                if (res) {
                    // console.log('Login successfully');
                    // console.log('name: ' + res.name);
                    this.error = '';
                    // this.$store.trainerId = res.id;
                    storage.setTrainerId(res.id);
                    this.$router.push({ name: 'List'});
                    // this.$router.push({ name: 'List', params: { trainerId: res.id } });
                } else {
                    this.error = "帳號或密碼輸入錯誤";
                }
            });
        }
    }
}
</script>

<style>
.login-container {
    background: linear-gradient( rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5) ), url('~@/assets/bg_login.jpeg') no-repeat center center fixed;
    background-size: cover;
}
input {
    text-align: center;
}
</style>