<template>
  <div class="row">
      <div class="col-auto">
          <button class="btn btn-previous h-100" @click="onPrevious" :disabled="!hasPreviousData"></button>
      </div>
      <div class="col my-auto">
          <div v-if="currentData">
              <div class="row align-items-center">
                  <div class="col">
                      <p class="h5 my-text m-0">測量日期 {{ new Date(currentData.date).toLocaleDateString() }}</p>
                  </div>
                  <div class="col-auto">
                      <button class="btn btn-edit rounded-circle my-primary-style" @click="onEdit"></button>
                  </div>
              </div>
          </div>
      </div>
      <div class="col-auto">
          <button class="btn btn-next h-100" @click="onNext" :disabled="!hasNextData"></button>
      </div>
  </div>
  <div class="row ps-4 pe-4">
      <div class="col-12">
          <p class="my-second-text h4 d-inline">BMI </p>
          <div class="d-inline" v-if="currentData">
            <p class="my-text display-1 fw-bold d-inline" ref="bmiDigitals">{{ bmiValue }}</p>
          </div>
          <i class="d-inline-block" :class="trendClass" ref="bmiTrend"></i>
      </div>
      <div v-if="currentData">
        <div v-for="item in fitnessDataItems" :key="item.id">
            <data-item-view 
            :icon="item.icon" 
            :title="item.name" 
            :value="currentData[item.key] ?? 0" 
            :unit="item.unit" 
            :isIncreasing="isValueIncreasing(item.key)"/>
            </div>
       </div>
  </div>
</template>

<script>
import DataItemView from '@/components/DataItemView.vue'
import FitnessDataItems from '@/json/fitness_data_items.json'
const animation = require('@/js/Animation.js');

export default {
    components: { DataItemView },
    expose: ['showLastData'],
    data: function() {
        return {
            bmiValue: 0
        }
    },
    watch: {
        bmiValue: function(newValue, oldValue) {
            // animation.countDigitals(this.$refs.bmiDigitals, oldValue, newValue, 10);
            if (newValue > oldValue) {
                animation.moveIn(this.$refs.bmiTrend, { direction: 'top', easing: 'easeOutElastic', scaleY: 1})
            } else if (newValue < oldValue) {
                animation.moveIn(this.$refs.bmiTrend, { direction: 'top', easing: 'easeOutElastic', scaleY: -1})
            }
        },
        currentData: function() {
            if (this.currentData) {
                let curWeight = this.currentData.weight;
                let value = this.bmi(curWeight, this.height);
                this.bmiValue = value;
            }
        }
    },
    computed: {
        trendClass: function() {
            if ((this.dataIndex - 1) >= 0) {
                let weight = this.currentData.weight;
                let preWeight = this.previousData.weight;
                let curBmi = this.bmi(weight, this.height);
                let preBmi = this.bmi(preWeight, this.height);
                if (curBmi > preBmi) {
                    return "icon-trend-up";
                } else if (curBmi < preBmi) {
                    return "icon-trend-down";
                } else {
                    return "icon-trend-empty";
                }
            } else {
                return 'icon-trend-empty';
            }
        }
    },
    props: {
        dataIndex: {
            type: Number,
            default: 0
        },
        fitnessDataItems: {
            type: Array,
            default: FitnessDataItems
        },
        height: Number,
        currentData: Object,
        previousData: Object,
        isIncreasing: {
            type: Boolean,
            default: true
        },
        hasPreviousData: {
            type: Boolean,
            default: false
        },
        hasNextData: {
            type: Boolean,
            default: false
        }
    },
    mounted: function() {
        let value = this.bmi(this.currentData.weight, this.height);
        this.bmiValue = value;
    },
    methods: {
        onNext: function() {
            this.$emit('increment-data-index');
        },
        onPrevious: function() {
            this.$emit('decrement-data-index');
        },
        isValueIncreasing: function(key) {
            if ((this.dataIndex - 1) >= 0) {
                let curValue = this.currentData[key];
                let preValue = this.previousData[key];
                if (curValue > preValue) {
                    return true;
                } else if (curValue < preValue) {
                    return false;
                } else {
                    return null;
                }
            } else {
                return null
            }
        },
        bmi: function(weight, height) {
            if (!height && !weight) { return '---' }
            let original = weight / Math.pow(height / 100, 2);
            let rounded = Math.round(original * 10) / 10;
            return rounded.toFixed(1)
        },
        showLastData: function() {
            // let lastIndex = this.fitnessData.length - 1;
            // this.dataIndex = lastIndex;
        },
        onEdit: function() {
            this.$emit('on-edit');
        }
    }
}
</script>

<style>
.btn-previous {
    height: 60px;
    width: 60px;
    background-image: url('~@/assets/icon_previous.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 50px;
}
.btn-next {
    height: 60px;
    width: 60px;
    background-image: url('~@/assets/icon_next.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 50px;
}
.icon-trend-up {
    height: 60px;
    width: 40px;
    background-image: url('~@/assets/icon_red_rising_arrow.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
}
.icon-trend-down {
    height: 60px;
    width: 40px;
    background-image: url('~@/assets/icon_green_rising_arrow.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
    transform: scaleY(-1);
}
.icon-trend-empty {
    height: 40px;
    width: 40px;
}
.btn-edit {
    height: 50px;
    width: 50px;
    background-image: url('~@/assets/icon_edit.png');
    background-repeat: no-repeat;
    background-position: center;
    background-size: 30px;
}
</style>